
import { defineComponent, ref } from 'vue';
import { OnClickOutside } from '@vueuse/components';

export default defineComponent({
    setup() {
        const mobileNav = ref(false);
        return {
            mobileNav,
            openWindow: (url: string) => { window.open(url); },
            close: () => {
                mobileNav.value = false;
            },
        };
    },
    components: {
        OnClickOutside,
    },
});
